@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200;400;700&display=swap);
*{
  font-family: 'Raleway', sans-serif;
}

:root{
  --clr-white:white;
  --clr-black:black;
  --clr-dark:#1e222c;
  --clr-light:#2f343a;
  --clr-main:#d91944;
  --text-color: white;
  --nav-size: 100px;
  --border:1px solid #5c5c5c;
  --border-radius:8px;
  --speed: 500ms;

}

body{
  color: white;
  background-color: #1e222c;
  background-color: var(--clr-dark);
}

.logo-brand{
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.faizi-logo svg{
  width: calc(100px*0.8);
  width: calc(var(--nav-size)*0.8);
  height: calc(100px*0.8);
  height: calc(var(--nav-size)*0.8);
}

ul{
  list-style: none;
  margin: 0;
  padding:0;
}

a{
  color: white !important;
  color: var(--text-color) !important;
  text-decoration:none !important;
}

.my-navbar{
  height: 100px;
  height: var(--nav-size);
  background-color: #1e222c;
  background-color: var(--clr-dark);
  padding: 0 1rem;
  border-bottom:1px solid #5c5c5c;
  border-bottom:var(--border);
}

.my-navbar-nav{
  max-width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.my-nav-item{
  width: calc(100px*0.7);
  width: calc(var(--nav-size)*0.7);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.icon-button{
  fill: white;
  fill: var(--clr-white);
  background-color: #2f343a;
  background-color: var(--clr-light);
  --button-size:calc(var(--nav-size)*0.5);
  width: var(--button-size);
  height: var(--button-size);
  border-radius: 20%;
  padding: 5px;
  margin: 2px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  transition: -webkit-filter 300ms;
  transition: filter 300ms;
  transition: filter 300ms, -webkit-filter 300ms;
}

.icon-button svg{
  width: calc(100px*0.25);
  width: calc(var(--nav-size)*0.25);
  height: calc(100px*0.25);
  height: calc(var(--nav-size)*0.25);
}

.icon-button:hover{
  -webkit-filter: brightness(1.2);
          filter: brightness(1.2);
  fill: #d91944;
  fill: var(--clr-main);
}

.faizi-logo{
  z-index: 2;
}
.faizi-logo:hover{
  -webkit-filter: brightness(1.1);
          filter: brightness(1.1);
}

.icon-button:active{
  fill: #d91944;
  fill: var(--clr-main);
}

.my-dropdown{
  
  position: absolute;
  top: calc(100px*0.95);
  top: calc(var(--nav-size)*0.95);
  width: 300px;
  -webkit-transform: translateX(-40%);
          transform: translateX(-40%);
  background-color: #1e222c;
  background-color: var(--clr-dark);
  border: 1px solid #5c5c5c;
  border: var(--border);
  border-radius: 8px;
  border-radius: var(--border-radius);
  padding: 1rem;
  overflow: hidden;

  transition: height 500ms ease;

  transition: height var(--speed) ease;
}

.my-menu-item{
  fill: white;
  fill: var(--clr-white);
  height: calc(100px*0.69);
  height: calc(var(--nav-size)*0.69);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  border-radius: calc(8px*2);
  border-radius: calc(var(--border-radius)*2);
  transition: background 500ms;
  transition: background var(--speed);
  padding: 0.5rem;
  margin: 0.3rem 0;
}

.my-menu-item:hover{
  background-color: #d91944;
  background-color: var(--clr-main);
}

.my-menu-item:hover .icon-button{
  fill: #d91944;
  fill: var(--clr-main);
}

.menu-item-text{
  margin: 0.5rem;
}

.icon-right{
  margin-left: auto;
}


.my-menu{
  width: 100%;
}

.my-menu-primary-enter{
  position: absolute;
  -webkit-transform: translateX(-110%);
          transform: translateX(-110%);

}

.my-menu-primary-enter-active{
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
  transition: all 500ms ease;
  transition: all var(--speed) ease;
}

.my-menu-primary-exit{
  position: absolute;
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

.my-menu-primary-exit-active{
  -webkit-transform: translateX(-110%);
          transform: translateX(-110%);
  transition: all 500ms ease;
  transition: all var(--speed) ease;
}

.my-menu-secondary-enter{
  -webkit-transform: translateX(110%);
          transform: translateX(110%);

}

.my-menu-secondary-enter-active{
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
  transition: all 500ms ease;
  transition: all var(--speed) ease;
}

.my-menu-secondary-exit{
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

.my-menu-secondary-exit-active{
  -webkit-transform: translateX(110%);
          transform: translateX(110%);
  transition: all 500ms ease;
  transition: all var(--speed) ease;
}
